<template>
  <!-- 产品信息里的弹框 -->
  <div class="container">
    <el-dialog
      title="产品规格信息"
      v-model="dialogVisible"
      width="1000px"
      top="5vh"
    >
      <div>
        <div class="contain">
          <div class="item_header">基本信息</div>
          <div class="item_two">
            <div class="code">
              <small>产品编码</small>
              <div class="code_box">
                <el-input
                  size="mini"
                  v-model="product_code"
                  placeholder=""
                ></el-input>
              </div>
              <div class="icon">
                <i>*</i>
              </div>
            </div>
            <div class="code">
              <small>产品缩写</small>
              <div class="code_box">
                <el-input
                  size="mini"
                  v-model="product_abbreviation"
                  placeholder=""
                ></el-input>
              </div>
              <div class="icon">
                <i></i>
              </div>
            </div>
          </div>
          <div class="item_two line_box">
            <div class="code">
              <small>产品规格</small>
              <div class="code_box">
                <el-input
                  size="mini"
                  v-model="product_specification"
                  placeholder=""
                ></el-input>
              </div>
              <div class="icon">
                <i>*</i>
              </div>
            </div>
            <div class="code">
              <small>推荐存储条件</small>
              <div class="code_box">
                <el-select
                  size="mini"
                  placeholder=""
                  class="newselect"
                  v-model="recommend_storageConditionsId"
                >
                  <el-option
                    v-for="item in recommend_storageConditionslist"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </div>
              <div class="icon">
                <i></i>
              </div>
            </div>
          </div>
        </div>
        <div class="footer">
          <div class="footer_title">
            <p>生产厂商列表</p>
            <img src="../../../image/new.png" alt="" @click="reset()" />
          </div>
          <div class="power_main">
            <el-table
              :data="tableData1"
              border
              style="width: 100%; height: 150px"
              size="mini"
              :height="$store.state.innerheight - 420"
              :header-cell-style="{ background: '#f5f5f5' }"
            >
              <el-table-column
                prop="manufacturer"
                label="生产厂商"
                width=""
              >
              </el-table-column>
              <el-table-column
                prop="article_number"
                label="货号"
                width=""
              >
              </el-table-column>
              <el-table-column prop="registeredNumber" label="注册证号" width="">
              </el-table-column>
              <el-table-column prop="storageConditions" label="储存条件" width="">
              </el-table-column>
              <el-table-column prop="term_of_validity" label="注册证有效期" width="">
              </el-table-column>
              <el-table-column label="操作" width="">
                <template #default="scope">
                  <el-button
                    @click="handleEdit(scope.row)"
                    type="text"
                    size="mini"
                    >编辑</el-button
                  >
                  <el-button
                    style="color: red"
                    type="text"
                    size="mini"
                    @click="handleDel(scope.row)"
                    >移除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="footer">
          <div class="footer_title">
            <p>单位列表</p>
            <img src="../../../image/new.png" alt="" @click="reset()" />
          </div>
          <div class="power_main">
            <el-table
              :data="tableData2"
              border
              style="width: 100%; height: 150px"
              size="mini"
              :height="$store.state.innerheight - 420"
              :header-cell-style="{ background: '#f5f5f5' }"
            >
              <el-table-column
                prop="conversion_molecule"
                label="换算分子"
                width=""
              >
              </el-table-column>
              <el-table-column
                prop="basic_unit"
                label="基本单位"
                width=""
              >
              </el-table-column>
              <el-table-column prop="conversion_denominator" label="换算分母" width="">
              </el-table-column>
              <el-table-column
                prop="conversion_unit"
                label="转换单位"
                width=""
              >
              </el-table-column>
              <el-table-column
                prop="standard_unit_price"
                label="标准单价"
                width=""
              >
              </el-table-column>
              <el-table-column label="操作" width="">
                <template #default="scope">
                  <el-button
                    @click="handleEdit(scope.row)"
                    type="text"
                    size="mini"
                    >编辑</el-button
                  >
                  <el-button
                    style="color: red"
                    type="text"
                    size="mini"
                    @click="handleDel(scope.row)"
                    >移除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" type="primary" @click="save">保 存</el-button>
          <el-button size="mini" @click="odd">取 消</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { reactive, toRefs } from "@vue/reactivity";
import { getCurrentInstance } from "@vue/runtime-core";

export default {
  setup() {
    const state = reactive({
      dialogVisible: false,
      product_code: "",
      product_abbreviation: "",
      product_specification: "",
      recommend_storageConditionsId: "",
      recommend_storageConditionslist: [],
      tableData1: [
          {
              manufacturer: "",
              article_number: "",
              registeredNumber: "",
              storageConditions: "",
              term_of_validity: "",
          },
          {
              manufacturer: "",
              article_number: "",
              registeredNumber: "",
              storageConditions: "",
              term_of_validity: "",
          },
          {
              manufacturer: "",
              article_number: "",
              registeredNumber: "",
              storageConditions: "",
              term_of_validity: "",
          },
      ],
      tableData2: [
          {
              conversion_molecule: "",
              basic_unit: "",
              conversion_denominator: "",
              conversion_unit: "",
              standard_unit_price: "",
          },
          {
              conversion_molecule: "",
              basic_unit: "",
              conversion_denominator: "",
              conversion_unit: "",
              standard_unit_price: "",
          },
          {
              conversion_molecule: "",
              basic_unit: "",
              conversion_denominator: "",
              conversion_unit: "",
              standard_unit_price: "",
          },
      ],
    });
    const { proxy } = getCurrentInstance();
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    const setedit = () => {
      proxy.dialogVisible = true;
    };
    const reset = () => {};
    const handleEdit = () => {};
    const handleDel = () => {};
    const save = () => {
      proxy.dialogVisible = false;
    };

    const odd = () => {
      proxy.dialogVisible = false;
    };

    return {
      setshow,
      setedit,
      ...toRefs(state),
      handleEdit,
      handleDel,
      odd,
      save,
      reset,
    };
  },
};
</script>
<style  scoped>
.contain {
  margin: 10px;
  border: 1px solid #e0e0e0;
  width: 900px;
}
.footer {
  margin: 10px;
  border: 1px solid #e0e0e0;
  width: 900px;
  height: 200px;
}
.power_main {
  padding: 0px 10px;
  box-sizing: border-box;
}
.item_header {
  font-size: 15px;
  font-weight: bold;
  padding: 10px;
  box-sizing: border-box;
}
.footer_title {
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}
.footer_title > p {
  font-size: 15px;
  font-weight: bold;
  margin: 0;
}
.footer_title > img {
  width: 20px;
}
.item_two {
  display: flex;
  align-items: center;
  padding-top: 10px;
}

.code_box {
  width: calc(100% - 120px);
}
.code {
  width: 49%;
  display: flex;
  align-items: center;
}
.code > small {
  width: 80px;
  text-align: right;
  margin-right: 5px;
}
.icon {
  color: red;
}
.line_box {
  margin-bottom: 10px;
}
</style>
