<template>
  <!-- 产品信息 -->
  <div class="container">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="产品信息" name="productInformation">
        <productdelails ref="productdelails"></productdelails>
      </el-tab-pane>
    </el-tabs>
    <div class="loginOut">
      <el-form-item>
        <el-button
          type="primary"
          size="mini"
          @click="saveBtn()"
          >保存</el-button
        >
        <el-button size="mini" @click="returnBtn()"
          >返回</el-button
        >
      </el-form-item>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import productdelails from "../product_management/productdelails.vue";
import { getCurrentInstance } from "@vue/runtime-core";
export default {
  components: {
    productdelails,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      activeName: "productInformation",
    });
    const saveBtn = () => {
      proxy.$router.push("/product_management");
    };
    const returnBtn = () => {
      proxy.$router.push("/product_management");
    };

    return {
      ...toRefs(state),
      saveBtn,
      returnBtn,
    };
  },
};
</script>

<style scoped>
.container {
  background-color: #fff;
  height: 100%;
}

.loginOut {
  position: absolute;
  right: 35px;
  top: 60px;
}
</style>