<script>
import { reactive, toRefs, onMounted } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";
import productDialog from "./productDialog.vue"
export default {
  components:{
    productDialog
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      product_name: "",
      product_classificationlist: [],
      product_classificationId: "",
      remarks: "",
      tableData: [
        {
          product_code: "PT0001",
          product_abbreviation: "148",
          product_specification: "水平1: 6×3ml",
          basic_unit: "盒",
          standard_unit_price:"200",
          manufacturer:"上海厂商",
          article_number:"145",
          registeredNumber:"GBW(E)090073",
          storageConditions:"冷藏",
          term_of_validity:"2022/6/25 0:00:00",
          recommend_storageConditions:"冷藏",
        },
        {
          product_code: "PT0001",
          product_abbreviation: "148",
          product_specification: "水平1: 6×3ml",
          basic_unit: "盒",
          standard_unit_price:"200",
          manufacturer:"北京厂商",
          article_number:"146",
          registeredNumber:"国制标物10001091号",
          storageConditions:"冷藏",
          term_of_validity:"2022/6/25 0:00:00",
          recommend_storageConditions:"冷藏",
        },
        {
          product_code: "PT0001",
          product_abbreviation: "148",
          product_specification: "水平1: 6×3ml",
          basic_unit: "盒",
          standard_unit_price:"200",
          manufacturer:"武汉厂商",
          article_number:"147",
          registeredNumber:"京械注准20162400101",
          storageConditions:"冷藏",
          term_of_validity:"2022/6/25 0:00:00",
          recommend_storageConditions:"冷藏",
        },
      ],
    });
    const reset = () => {
      proxy.$refs.productDialog.setshow();
    };

    const handleEdit = () => {
      proxy.$refs.productDialog.setedit();
    };
    const handleDel = () => {};
    const upload = () => {};
    // 合并表格的根据某个id值
    const objectspanmethod = ({row,column,rowIndex,columnIndex}) =>{
      console.log(row)
      console.log(column)
      for(var i = 0; i < 5; i++){
        if(i==columnIndex) {
          const _row = proxy.spanArr[rowIndex]
          const _col = _row > 0 ? 1 : 0
          return {
            rowspan:_row,
            colspan:_col
          }
        }
      }
      if(columnIndex==10){
        const _row = proxy.spanArr[rowIndex]
          const _col = _row > 0 ? 1 : 0
          return {
            rowspan:_row,
            colspan:_col
          }
      }
    }
    // 先把获取到的数据按规定值相同的放一起
    const rowspan = () => {
      proxy.spanArr = []
      proxy.position = 0
      proxy.tableData.forEach((item,index) => {
        if(0 == index) {
          proxy.spanArr.push(1)
          proxy.position = 0
        }else{
          if(proxy.tableData[index].code == proxy.tableData[index - 1].code){
            proxy.spanArr[proxy.position] += 1
            proxy.spanArr.push(0)
          }else{
            proxy.spanArr.push(1)
            proxy.position = index
          }
        }
      })
      console.log(proxy.spanArr)
    }
    onMounted(()=>{
      proxy.rowspan()
    })
    return {
      ...toRefs(state),
      handleEdit,
      handleDel,
      reset,
      upload,
      objectspanmethod,
      rowspan,
    };
  },
};
</script>

